'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { useAuth } from '@/lib/auth-context'
import { MobileNav } from '@/components/mobile-nav'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent } from '@/components/ui/card'
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar'
import { ArrowLeft, Camera, Save, MapPin } from 'lucide-react'
import { useToast } from '@/hooks/use-toast'

export default function EditProfilePage() {
  const { user, isLoading } = useAuth()
  const router = useRouter()
  const { toast } = useToast()
  const [photoPreview, setPhotoPreview] = useState<string | null>(null)
  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    photoUrl: '',
    location: {
      address: '',
      city: '',
      lat: 0,
      lng: 0,
    },
    currentPassword: '',
    newPassword: '',
    confirmPassword: '',
  })

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login')
    }
    if (user) {
      setFormData({
        name: user.name,
        phone: user.phone,
        photoUrl: user.photoUrl || '',
        location: (user as any).location || {
          address: '',
          city: '',
          lat: 0,
          lng: 0,
        },
        currentPassword: '',
        newPassword: '',
        confirmPassword: '',
      })
      setPhotoPreview(user.photoUrl || null)
    }
  }, [user, isLoading, router])

  const handlePhotoChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      const reader = new FileReader()
      reader.onloadend = () => {
        setPhotoPreview(reader.result as string)
        setFormData({ ...formData, photoUrl: reader.result as string })
      }
      reader.readAsDataURL(file)
    }
  }

  const getCurrentLocation = () => {
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition(
        async (position) => {
          const { latitude, longitude } = position.coords
          
          // In a real app, you would use a geocoding service to get the address
          // For now, we'll just update the coordinates
          const updatedLocation = {
            ...formData.location,
            lat: latitude,
            lng: longitude,
            address: formData.location.address || `${latitude.toFixed(4)}, ${longitude.toFixed(4)}`,
          }

          setFormData({ ...formData, location: updatedLocation })

          // Send location to server (development placeholder)
          try {
            if (user && user.id) {
              await fetch('/api/users/location', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                  userId: user.id,
                  lat: latitude,
                  lng: longitude,
                  address: updatedLocation.address,
                  city: updatedLocation.city,
                }),
              })
            }

            toast({
              title: 'Localização obtida!',
              description: 'Sua localização foi atualizada com sucesso.',
            })
          } catch (err) {
            toast({
              title: 'Erro ao enviar localização',
              description: 'Não foi possível salvar sua localização no servidor.',
              variant: 'destructive',
            })
          }
        },
        (error) => {
          toast({
            title: 'Erro ao obter localização',
            description: 'Não foi possível obter sua localização. Verifique as permissões.',
            variant: 'destructive',
          })
        }
      )
    } else {
      toast({
        title: 'Geolocalização não suportada',
        description: 'Seu navegador não suporta geolocalização.',
        variant: 'destructive',
      })
    }
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    
    if (formData.newPassword) {
      if (!formData.currentPassword) {
        toast({
          title: 'Erro',
          description: 'Por favor, informe sua senha atual.',
          variant: 'destructive',
        })
        return
      }
      
      if (formData.newPassword !== formData.confirmPassword) {
        toast({
          title: 'Erro',
          description: 'As senhas não coincidem.',
          variant: 'destructive',
        })
        return
      }
      
      if (formData.newPassword.length < 6) {
        toast({
          title: 'Erro',
          description: 'A nova senha deve ter pelo menos 6 caracteres.',
          variant: 'destructive',
        })
        return
      }
    }
    
    ;(async () => {
      try {
        // In a real app, this would make an API call to update the user and save location
        if (user && user.id && formData.location?.lat && formData.location?.lng) {
          await fetch('/api/users/location', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
              userId: user.id,
              lat: formData.location.lat,
              lng: formData.location.lng,
              address: formData.location.address,
              city: formData.location.city,
            }),
          })
        }

        toast({
          title: 'Perfil atualizado!',
          description: 'Suas informações foram salvas com sucesso.',
        })

        setTimeout(() => {
          router.push('/profile')
        }, 1000)
      } catch (err) {
        toast({
          title: 'Erro',
          description: 'Falha ao salvar informações. Tente novamente.',
          variant: 'destructive',
        })
      }
    })()
  }

  const getInitials = (name: string) => {
    return name
      .split(' ')
      .map((n) => n[0])
      .join('')
      .toUpperCase()
      .slice(0, 2)
  }

  if (isLoading || !user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-primary border-t-transparent rounded-full animate-spin mx-auto mb-4" />
          <p className="text-muted-foreground">Carregando...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-teal-50 to-orange-50 pb-20">

      <main className="container mx-auto px-3 py-3 sm:px-4 sm:py-6 max-w-2xl">
        <div className="mb-3 flex items-center gap-2">
          <Button
            variant="ghost"
            size="sm"
            onClick={() => router.back()}
            className="p-2 h-auto"
          >
            <ArrowLeft className="w-5 h-5" />
          </Button>
          <h1 className="text-base sm:text-xl font-bold">Editar Perfil</h1>
        </div>
        <Card>
          <CardContent className="p-4 sm:p-6">
            <form onSubmit={handleSubmit} className="space-y-4 sm:space-y-6">
              {/* Photo Upload */}
              <div className="flex flex-col items-center mb-6">
                <div className="relative">
                  <Avatar className="w-24 h-24 sm:w-32 sm:h-32">
                    <AvatarImage src={photoPreview || user.photoUrl || "/placeholder.svg"} alt={user.name} />
                    <AvatarFallback className="bg-teal-600 text-white text-2xl">
                      {getInitials(formData.name || user.name)}
                    </AvatarFallback>
                  </Avatar>
                  <label
                    htmlFor="photo-upload"
                    className="absolute bottom-0 right-0 bg-teal-600 text-white rounded-full p-2 cursor-pointer hover:bg-teal-700 transition-colors shadow-lg"
                  >
                    <Camera className="w-4 h-4" />
                    <input
                      id="photo-upload"
                      type="file"
                      accept="image/*"
                      className="hidden"
                      onChange={handlePhotoChange}
                    />
                  </label>
                </div>
                <p className="text-xs text-muted-foreground mt-2 text-center">
                  Clique na câmera para alterar a foto
                </p>
              </div>

              {/* Name Field */}
              <div className="space-y-2">
                <Label htmlFor="name">Nome completo</Label>
                <Input
                  id="name"
                  type="text"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  required
                  className="h-10"
                />
              </div>


              {/* Phone Field */}
              <div className="space-y-2">
                <Label htmlFor="phone">Telefone</Label>
                <Input
                  id="phone"
                  type="tel"
                  value={formData.phone}
                  onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                  placeholder="(00) 00000-0000"
                  required
                  className="h-10"
                />
              </div>

              <div className="space-y-3 pt-2 border-t">
                <div className="flex items-center justify-between">
                  <Label className="text-base font-semibold">Localização</Label>
                  <Button
                    type="button"
                    variant="outline"
                    size="sm"
                    onClick={getCurrentLocation}
                    className="h-8 text-xs"
                  >
                    <MapPin className="w-3.5 h-3.5 mr-1" />
                    Usar minha localização
                  </Button>
                </div>
                <p className="text-xs text-muted-foreground">
                  Sua localização é usada para notificar sobre pets próximos a você
                </p>
                
                <div className="space-y-2">
                  <Label htmlFor="address">Endereço</Label>
                  <Input
                    id="address"
                    type="text"
                    value={formData.location.address}
                    onChange={(e) => setFormData({
                      ...formData,
                      location: { ...formData.location, address: e.target.value }
                    })}
                    placeholder="Rua, número, bairro"
                    className="h-10"
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="city">Cidade</Label>
                  <Input
                    id="city"
                    type="text"
                    value={formData.location.city}
                    onChange={(e) => setFormData({
                      ...formData,
                      location: { ...formData.location, city: e.target.value }
                    })}
                    placeholder="Cidade - Estado"
                    className="h-10"
                  />
                </div>
              </div>

              <div className="space-y-3 pt-2 border-t">
                <Label className="text-base font-semibold">Alterar Senha</Label>
                <p className="text-xs text-muted-foreground">
                  Deixe em branco se não deseja alterar sua senha
                </p>
                
                <div className="space-y-2">
                  <Label htmlFor="currentPassword">Senha atual</Label>
                  <Input
                    id="currentPassword"
                    type="password"
                    value={formData.currentPassword}
                    onChange={(e) => setFormData({ ...formData, currentPassword: e.target.value })}
                    placeholder="Digite sua senha atual"
                    className="h-10"
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="newPassword">Nova senha</Label>
                  <Input
                    id="newPassword"
                    type="password"
                    value={formData.newPassword}
                    onChange={(e) => setFormData({ ...formData, newPassword: e.target.value })}
                    placeholder="Digite sua nova senha"
                    className="h-10"
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="confirmPassword">Confirmar nova senha</Label>
                  <Input
                    id="confirmPassword"
                    type="password"
                    value={formData.confirmPassword}
                    onChange={(e) => setFormData({ ...formData, confirmPassword: e.target.value })}
                    placeholder="Confirme sua nova senha"
                    className="h-10"
                  />
                </div>
              </div>

              {/* Action Buttons */}
              <div className="flex gap-3 pt-4">
                <Button
                  type="button"
                  variant="outline"
                  className="flex-1 h-10"
                  onClick={() => router.back()}
                >
                  Cancelar
                </Button>
                <Button type="submit" className="flex-1 h-10">
                  <Save className="w-4 h-4 mr-2" />
                  Salvar
                </Button>
              </div>
            </form>
          </CardContent>
        </Card>
      </main>

      <MobileNav />
    </div>
  )
}
